import React, {Component} from 'react';
export default class Header extends Component {
    render() {
        return (
            <React.Fragment>
                {/*generated code*/}
                <header id="home">
                    <nav id="nav-wrap">
                        <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
                        <a className="mobile-btn" href="#" title="Hide navigation">Hide navigation</a>
                        <ul id="nav" className="nav">
                            <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
                            <li><a className="smoothscroll" href="#resume">Resume</a></li>
                            <li><a className="smoothscroll" href="#portfolio">Works</a></li>
                        </ul>
                        {/* end #nav */}
                    </nav>
                    {/* end #nav-wrap */}
                    <div className="row banner">
                        <div className="banner-text">
                            <h1 className="responsive-headline">I'm Nikita Ilyukov.</h1>
                            <h3>
                                I'm a <span>junior developer</span> and I want <span>to find a job</span> to improve my skills.
                                <br/>
                                <span>Ready for work and defense.</span>
                            </h3>
                            <hr />
                            <ul className="social">
                                <li><a href="https://www.linkedin.com/in/nikita-ilyukov-820a48130/" rel="noopener noreferrer" target="_blank"><i className="fab fa-linkedin-in"/></a></li>
                                <li><a href="https://github.com/nilyukov" rel="noopener noreferrer" target="_blank"><i className="fab fa-github"/></a></li>
                                <li><a href="https://t.me/nik_purple" rel="noopener noreferrer" target="_blank"><i className="fab fa-telegram-plane"/></a></li>
                            </ul>
                            <p className="address">
                                <span>+375 (029) 389-98-05</span><br />
                                <span>nilyukov@gmail.com</span>
                            </p>
                        </div>
                    </div>
                    <p className="scrolldown">
                        <a className="smoothscroll" href="#resume"><i className="icon-down-circle"/></a>
                    </p>
                </header>
            </React.Fragment>
        );
    }
}
