import React, {Component} from 'react';
export default class Resume extends Component {
    render() {
        return (
            <React.Fragment>
                {/*generated code*/}
                <section id="resume">
                    {/* Education
                     ----------------------------------------------- */}
                    <div className="row education">
                        <div className="three columns header-col">
                            <h1><span>Education</span></h1>
                        </div>
                        <div className="nine columns main-col">
                            <div className="row item">
                                <div className="twelve columns">
                                    <h3>Belarusian State University</h3>
                                    <p className="info"><em className="date">June
                                        2018</em></p>
                                    <p>
                                        Specialty: Mathematics and Information Technologies <br/>
                                        Direction: Web-Programming and Internet Technologies <br/>
                                        Qualification: Mathematician. IT-specialist
                                    </p>
                                </div>
                            </div>
                            {/* item end */}
                        </div>
                        {/* main-col end */}
                    </div>
                    {/* End Education */}
                    {/* Work
                     ----------------------------------------------- */}
                    <div className="row work">
                        <div className="three columns header-col">
                            <h1><span>Traineeship</span></h1>
                        </div>
                        <div className="nine columns main-col">
                            <div className="row item">
                                <div className="twelve columns">
                                    <h3>Forte Group</h3>
                                    <p className="info">UI/UX Trainee<span>â€¢</span> <em className="date">July 2018</em></p>
                                    <p>
                                        In this company I got experience with a JavaScript Library called Knockout. It is the MVVM pattern with templates. I created a web application with Bootstrap 3 framework, observable variables and loading json-data from the Pixabay API (collection of free high quality images and videos).
                                    </p>
                                </div>
                            </div>
                            {/* item end */}
                        </div>
                        {/* main-col end */}
                    </div>
                    {/* End Work */}
                    {/* Skills
                     ----------------------------------------------- */}
                    <div className="row skill">
                        <div className="three columns header-col">
                            <h1><span>Skills</span></h1>
                        </div>
                        <div className="nine columns main-col">
                            <p>I studied programming at the university, as well as on my own through open educational resources. Here are some of them: geekbrains, udemy, chanels on youtube and others.
                            </p>
                            <div className="bars">
                                <ul className="skills">
                                    <li><span className="bar-expand css"/><em>CSS3</em></li>
                                    <li><span className="bar-expand html5"/><em>HTML5</em></li>
                                    <li><span className="bar-expand javascript"/><em>JavaScript</em></li>
                                    <li><span className="bar-expand jquery"/><em>jQuery</em></li>
                                    <li><span className="bar-expand react"/><em>React</em></li>
                                    <li><span className="bar-expand php"/><em>PHP</em></li>
                                    <li><span className="bar-expand wordpress"/><em>Wordpress</em></li>

                                </ul>
                            </div>
                            {/* end skill-bars */}
                        </div>
                        {/* main-col end */}
                    </div>
                    {/* End skills */}
                </section>
            </React.Fragment>
        );
    }
}