import React, {Component} from 'react';
export default class Portfolio extends Component {
    render() {
        return (
            <React.Fragment>
                {/*generated code*/}
                <section id="portfolio">
                    <div className="row">
                        <div className="twelve columns collapsed">
                            <h1>Check Out Some of My Works.</h1>
                            {/* portfolio-wrapper */}
                            <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
                                <div className="columns portfolio-item">
                                    <div className="item-wrap">
                                        <a href="#modal-01">
                                            <img alt="Project" src="images/portfolio/runbikes.jpg"/>
                                            <div className="overlay">
                                                <div className="portfolio-item-meta">
                                                    <h5>Runbikes</h5>
                                                </div>
                                            </div>
                                            <div className="link-icon"><i className="icon-plus"/></div>
                                        </a>
                                    </div>
                                </div>
                                {/* item end */}
                                <div className="columns portfolio-item">
                                    <div className="item-wrap">
                                        <a href="#modal-04">
                                            <img alt="Project" src="images/portfolio/forte.jpg"/>
                                            <div className="overlay">
                                                <div className="portfolio-item-meta">
                                                    <h5>Knockout</h5>
                                                </div>
                                            </div>
                                            <div className="link-icon"><i className="icon-plus"/></div>
                                        </a>
                                    </div>
                                </div>
                                {/* item end */}
                                <div className="columns portfolio-item">
                                    <div className="item-wrap">
                                        <a href="#modal-02">
                                            <img alt="Project" src="images/portfolio/chemsafety.jpg"/>
                                            <div className="overlay">
                                                <div className="portfolio-item-meta">
                                                    <h5>Rotterdam Convention</h5>
                                                </div>
                                            </div>
                                            <div className="link-icon"><i className="icon-plus"/></div>
                                        </a>
                                    </div>
                                </div>
                                {/* item end */}

                                <div className="columns portfolio-item">
                                    <div className="item-wrap">
                                        <a href="#modal-03">
                                            <img alt="Project" src="images/portfolio/trend.jpg"/>
                                            <div className="overlay">
                                                <div className="portfolio-item-meta">
                                                    <h5>Affiliate system</h5>
                                                </div>
                                            </div>
                                            <div className="link-icon"><i className="icon-plus"/></div>
                                        </a>
                                    </div>
                                </div>
                                {/* item end */}
                                <div className="columns portfolio-item">
                                    <div className="item-wrap">
                                        <a href="#modal-05">
                                            <img alt="Project" src="images/portfolio/smitler.jpg"/>
                                            <div className="overlay">
                                                <div className="portfolio-item-meta">
                                                    <h5>Beauty salon</h5>
                                                </div>
                                            </div>
                                            <div className="link-icon"><i className="icon-plus"/></div>
                                        </a>
                                    </div>
                                </div>
                                {/* item end */}
                                <div className="columns portfolio-item">
                                    <div className="item-wrap">
                                        <a href="#modal-06">
                                            <img alt="Project" src="images/portfolio/grid.png"/>
                                            <div className="overlay">
                                                <div className="portfolio-item-meta">
                                                    <h5>Css Grid</h5>
                                                </div>
                                            </div>
                                            <div className="link-icon"><i className="icon-plus"/></div>
                                        </a>
                                    </div>
                                </div>
                                {/* item end */}

                                <div className="columns portfolio-item">
                                    <div className="item-wrap">
                                        <a href="#modal-07">
                                            <img alt="Project" src="images/portfolio/materials.png"/>
                                            <div className="overlay">
                                                <div className="portfolio-item-meta">
                                                    <h5>Construction Materials</h5>
                                                </div>
                                            </div>
                                            <div className="link-icon"><i className="icon-plus"/></div>
                                        </a>
                                    </div>
                                </div>
                            </div>
                            {/* portfolio-wrapper end */}
                        </div>
                        {/* twelve columns end */}
                        {/* Modal Popup
                         --------------------------------------------------------------- */}
                        <div id="modal-01" className="popup-modal mfp-hide">
                            <img className="scale-with-grid" src="images/portfolio/runbikes.jpg" alt="Project"/>
                            <div className="description-box">
                                <h4>Runbikes</h4>
                                <p>Online store runbikes on pure html / css and jquery. Used flex, slick slider. (Navigating pages in list.html)</p>
                                <span className="categories"><i className="fa fa-tag"/>HTML, CSS, Jquery</span>
                            </div>
                            <div className="link-box">
                                <a href="https://runbikes.firebaseapp.com/">Link</a>
                                <a className="popup-modal-dismiss">Close</a>
                            </div>
                        </div>
                        {/* modal-01 End */}
                        <div id="modal-02" className="popup-modal mfp-hide">
                            <img className="scale-with-grid" src="images/portfolio/chemsafety.jpg" alt="Project"/>
                            <div className="description-box">
                                <h4>Rotterdam Convention</h4>
                                <p>The site is designed on bootstrap 4 and wordpress.</p>
                                <span className="categories"><i className="fa fa-tag"/>Bootstrap 4, Wordpress</span>
                            </div>
                            <div className="link-box">
                                <a href="http://chemsafety.rspch.by/">Link</a>
                                <a className="popup-modal-dismiss">Close</a>
                            </div>
                        </div>
                        {/* modal-02 End */}
                        <div id="modal-03" className="popup-modal mfp-hide">
                            <img className="scale-with-grid" src="images/portfolio/trend.jpg" alt="Project"/>
                            <div className="description-box">
                                <h4>Affiliate system</h4>
                                <p>Landing page on pure html/css</p>
                                <span className="categories"><i className="fa fa-tag"/>Html, Css, Jquery</span>
                            </div>
                            <div className="link-box">
                                <a href="https://trend-aa1f1.firebaseapp.com/">Link</a>
                                <a className="popup-modal-dismiss">Close</a>
                            </div>
                        </div>
                        {/* modal-03 End */}
                        <div id="modal-04" className="popup-modal mfp-hide">
                            <img className="scale-with-grid" src="images/portfolio/forte.jpg" alt="Project"/>
                            <div className="description-box">
                                <h4>Knockout</h4>
                                <p>The site is designed on bootstrap 3 and knockoutjs</p>
                                <span className="categories"><i className="fa fa-tag"/>Bootstrap 3, Knockout</span>
                            </div>
                            <div className="link-box">
                                <a href="https://github.com/nilyukov/knockoutjs">Link</a>
                                <a className="popup-modal-dismiss">Close</a>
                            </div>
                        </div>
                        {/* modal-04 End */}
                        <div id="modal-05" className="popup-modal mfp-hide">
                            <img className="scale-with-grid" src="images/portfolio/smitler.jpg" alt="Project"/>
                            <div className="description-box">
                                <h4>Beauty salon</h4>
                                <p>The site is designed on bootstrap grid system, html/css, owl-carousel and etc.</p>
                                <span className="categories"><i className="fa fa-tag"/>Html, Css, Jquery</span>
                            </div>
                            <div className="link-box">
                                <a href="https://smitler-salon.firebaseapp.com/">Link</a>
                                <a className="popup-modal-dismiss">Close</a>
                            </div>
                        </div>
                        {/* modal-05 End */}
                        <div id="modal-06" className="popup-modal mfp-hide">
                            <img className="scale-with-grid" src="images/portfolio/grid.png" alt="Project"/>
                            <div className="description-box">
                                <h4>Css Grid</h4>
                                <p>Learning Css Grid.</p>
                                <span className="categories"><i className="fa fa-tag"/>Html, Css, Css Grid</span>
                            </div>
                            <div className="link-box">
                                <a href="https://github.com/nilyukov/grid">Link</a>
                                <a className="popup-modal-dismiss">Close</a>
                            </div>
                        </div>
                        {/* modal-06 End */}
                        <div id="modal-07" className="popup-modal mfp-hide">
                            <img className="scale-with-grid" src="images/portfolio/materials.png" alt="Project"/>
                            <div className="description-box">
                                <h4>Construction Materials</h4>
                                <p>Online store Materials on pure html / css and jquery. Used flex, slick slider and etc/</p>
                                <span className="categories"><i className="fa fa-tag"/>Html, Css</span>
                            </div>
                            <div className="link-box">
                                <a href="https://github.com/nilyukov/materials">Link</a>
                                <a className="popup-modal-dismiss">Close</a>
                            </div>
                        </div>


                    </div>
                    {/* row End */}
                </section>
            </React.Fragment>
        );
    }
}

